import React, { Component } from "react";
import { withRouter } from "react-router-dom";

import { BASEURL } from "../helpurl";
import Alert from "@material-ui/lab/Alert";

import axios from "axios";
axios.defaults.withCredentials = true;

// TODO:         
class BasicInputPost extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: null,
      errorMessage: null,
    };
  }

  // input     setState 
  handleInputGoal = (e) => {
    let value = e.target.value;
    this.setState({
      value: value,
    });
  };

  //       axios 
  handleBasicPost = (e) => {
    let key = e.target.value;
    let part_name = e.target.name;
    let part_value = null;
    const { value } = this.state;
    if (value) {
      part_value = value;
    } else {
      return this.setState({
        errorMessage: "  ",
      });
    }
    axios
      .post(`${BASEURL}/data/write`, {
        value: part_value,
        part_name: part_name,
      })
      .then((res) => {
        //    
        this.props.closeInputBodyPost(key);
        //      setState 
        this.props.handleRecentBody();
      })
      // TODO:     
      .catch((err) => {
        console.log(err.message);
      });
  };

  //    BasicInputPost  
  handlecloseInput = (e) => {
    let key = e.target.name;
    this.props.closeInputBodyPost(key);
  };

  render() {
    const { name, what } = this.props;
    return (
      <>
        <input
          className="p-1 text-sm border-b border-solid border-gray-300 focus:outline-none focus:text-gray"
          type="text"
          name={name}
          placeholder="  "
          onChange={this.handleInputGoal}
        />
        <button
          className="focus:outline-none shadow-md ml-1 bg-gray-200 text-gray-700 hover:bg-gray-300 rounded p-1 text-xs"
          name={name}
          value={what}
          onClick={this.handleBasicPost}
        >
          SAVE
        </button>
        <button
          className="focus:outline-none shadow-md ml-1 bg-gray-200 text-gray-700 hover:bg-gray-300 rounded p-1 text-xs"
          name={what}
          onClick={this.handlecloseInput}
        >
          CANCEL
        </button>
        {/* <div>
          <Alert severity="error">This is an error alert  check it out!</Alert>
          {this.state.errorMessage}
        </div> */}
      </>
    );
  }
}

export default withRouter(BasicInputPost);
